<template>
  <div class="view aside">
    <aside class="pickup">
      <c-pickup-tree :treeOptions="treeOptions" @node-click="handleNodeClick"></c-pickup-tree>
    </aside>
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="save" type="primary" class="button">保存</el-button>
        {{model}}
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames" @change="handleChange">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item
          :title="panelDsl.title"
          :name="panelDsl.title"
          v-for="(panelDsl, panelDslIndex) in dslArray"
          :key="'panelDsl'+panelDslIndex"
        >
          <c-table
            class="box-card"
            v-model="model"
            :dsl="panelDsl[model.lx]||[]"
            :rules="rules"
            :tableInfo="panelDsl"
            ref="topTabe"
          />
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import { dslArray, asideTree, model, rules } from "./mock.js";

export default {
  name: "Main",
  mounted() {
    this.activeNames = this.dslArray.map(panel => {
      return panel.title;
    });
    this.activeNames.push("datagrid");
  },
  data() {
    return {
      rules,
      dslArray,
      asideTree,
      activeNames: ["项目参数"],
      /* 动态数据 */
      model
    };
  },
  methods: {
    save() {
      function isOk(tables) {
        let count = tables.length;
        let isAllOk = true;
        return new Promise(resolve => {
          tables.map(table => {
            table.valid().then(isSuccess => {
              --count;
              isAllOk = isAllOk && isSuccess;
              if (!isAllOk || count === 0) resolve(isAllOk);
            });
          });
        });
      }
      /* 验证表单是否都符合rule */
      isOk(this.$refs.topTabe).then(isAllOk => {
        let doc = this.model;
        if (isAllOk) {
          const loading = this.$loading();
          console.log("this.model");
          /* save */
          this.$post("/proxy/save", {
            coll: "hqbj_dmgl_xtsz" /* collection */,
            doc /* document */
          }).then(response => {
            debugger;
            if (response.isSuccess) {
              let newTree = Array.prototype.map.call(
                this.asideTree,
                project => {
                  if (project.mc === this.model.mc) {
                    project = { ...this.model };
                  }
                  return project;
                }
              );

              this.asideTree = newTree;
              loading.close();
              this.$message({
                showClose: true,
                message: "保存成功",
                type: "success"
              });
            } else {
              this.$message({
                showClose: true,
                message: "保存失败",
                type: "warning"
              });
            }
          });
        } else {
          this.$message({
            showClose: true,
            message: "请按照提示检查输入项",
            type: "warning"
          });
        }
      });
    },
    deleteTableRow(row, index) {
      console.log("deleteTableRow", row, index);
    },
    console(...args) {
      console.log(args);
    },
    submit() {
      this.$refs.hytable[0].valid().then(isSuccess => {
        if (isSuccess) {
          this.$notify({
            title: "提示",
            message: "数据校验成功，可以提交表单"
          });
        }
      });
    },
    handleChange(activeNames) {
      console.log("activeNames", activeNames, activeNames === this.activeNames);
    },
    handleNodeClick(data, node, vm) {
      this.model = { ...data };

      const STRATEGE = {
        combobox: () => {
          let dataString = this.model.qzfw;
          let arr1 = dataString.split(";");
          let comboboxData = arr1.map(dataString2 => {
            let arr2 = dataString2.split(",");
            return {
              text: arr2[1],
              value: arr2[0]
            };
          });
          this.dslArray[0].combobox[0][1].items[0].data = comboboxData;
          this.dslArray = JSON.parse(JSON.stringify(this.dslArray));
          console.log("comboboxData", comboboxData);
        }
      };

      STRATEGE[this.model.lx] && STRATEGE[this.model.lx]();
    }
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "text"
        },
        showCheckbok: false
      };
      treeOptions.data = this.asideTree;
      return treeOptions;
    }
  },
  components: {
    CTable: () =>
      import(/* webpackChunkName: "FormTable" */ "@c/FormTable.vue"),
    CPickupTree: () =>
      import(/* webpackChunkName: "PickupTree" */ "@c/PickupTree.vue")
  }
};
</script>

